/**
 * CheckInButton - 
 * 
 */
import React, { useRef, useEffect } from 'react';
import {
    View,
    Text,
    TouchableOpacity,
    StyleSheet,
    Animated,
    ViewStyle,
} from 'react-native';
import { COLORS, FONTS, SPACING, CHECK_IN_BUTTON, SHADOWS } from '../../theme';

interface CheckInButtonProps {
    /**  */
    isCheckedIn: boolean;
    /**  */
    onPress: () => void;
    /**  */
    disabled?: boolean;
    /**  */
    style?: ViewStyle;
}

/**
 * 
 * @param isCheckedIn 
 * @param onPress 
 * @param disabled 
 * @param style 
 */
const CheckInButton: React.FC<CheckInButtonProps> = ({
    isCheckedIn,
    onPress,
    disabled = false,
    style,
}) => {
    // 
    const scaleAnim = useRef(new Animated.Value(1)).current;
    const pulseAnim = useRef(new Animated.Value(1)).current;
    const checkAnim = useRef(new Animated.Value(isCheckedIn ? 1 : 0)).current;

    // 
    useEffect(() => {
        if (!isCheckedIn) {
            const pulse = Animated.loop(
                Animated.sequence([
                    Animated.timing(pulseAnim, {
                        toValue: 1.1,
                        duration: 1000,
                        useNativeDriver: true,
                    }),
                    Animated.timing(pulseAnim, {
                        toValue: 1,
                        duration: 1000,
                        useNativeDriver: true,
                    }),
                ])
            );
            pulse.start();
            return () => pulse.stop();
        } else {
            pulseAnim.setValue(1);
        }
    }, [isCheckedIn, pulseAnim]);

    // 
    useEffect(() => {
        Animated.timing(checkAnim, {
            toValue: isCheckedIn ? 1 : 0,
            duration: 300,
            useNativeDriver: true,
        }).start();
    }, [isCheckedIn, checkAnim]);

    /**
     * 
     * 
     */
    const handlePressIn = () => {
        Animated.spring(scaleAnim, {
            toValue: 0.95,
            useNativeDriver: true,
        }).start();
    };

    /**
     * 
     */
    const handlePressOut = () => {
        Animated.spring(scaleAnim, {
            toValue: 1,
            friction: 3,
            tension: 40,
            useNativeDriver: true,
        }).start();
    };

    return (
        <View style={[styles.container, style]}>
            {/*  */}
            <Animated.View
                style={[
                    styles.pulseRing,
                    {
                        transform: [{ scale: pulseAnim }],
                        opacity: isCheckedIn ? 0 : 0.3,
                    },
                ]}
            />

            {/*  */}
            <TouchableOpacity
                activeOpacity={0.9}
                onPress={onPress}
                onPressIn={handlePressIn}
                onPressOut={handlePressOut}
                disabled={disabled}
            >
                <Animated.View
                    style={[
                        styles.button,
                        isCheckedIn && styles.buttonChecked,
                        { transform: [{ scale: scaleAnim }] },
                    ]}
                >
                    {/*  */}
                    <View
                        style={[
                            styles.innerCircle,
                            isCheckedIn && styles.innerCircleChecked,
                        ]}
                    >
                        {isCheckedIn ? (
                            //  - 
                            <Animated.Text
                                style={[
                                    styles.checkIcon,
                                    {
                                        opacity: checkAnim,
                                        transform: [
                                            {
                                                scale: checkAnim.interpolate({
                                                    inputRange: [0, 1],
                                                    outputRange: [0.5, 1],
                                                }),
                                            },
                                        ],
                                    },
                                ]}
                            >
                                
                            </Animated.Text>
                        ) : (
                            //  - 
                            <Text style={styles.faceIcon}></Text>
                        )}
                    </View>

                    {/*  */}
                    <Text style={[styles.statusText, isCheckedIn && styles.statusTextChecked]}>
                        {isCheckedIn ? '' : ''}
                    </Text>
                </Animated.View>
            </TouchableOpacity>
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        alignItems: 'center',
        justifyContent: 'center',
    },
    pulseRing: {
        position: 'absolute',
        width: CHECK_IN_BUTTON.size + 40,
        height: CHECK_IN_BUTTON.size + 40,
        borderRadius: (CHECK_IN_BUTTON.size + 40) / 2,
        backgroundColor: COLORS.primary,
    },
    button: {
        width: CHECK_IN_BUTTON.size,
        height: CHECK_IN_BUTTON.size,
        borderRadius: CHECK_IN_BUTTON.size / 2,
        backgroundColor: COLORS.primary,
        alignItems: 'center',
        justifyContent: 'center',
        ...SHADOWS.lg,
    },
    buttonChecked: {
        backgroundColor: COLORS.gray300,
    },
    innerCircle: {
        width: CHECK_IN_BUTTON.innerSize,
        height: CHECK_IN_BUTTON.innerSize,
        borderRadius: CHECK_IN_BUTTON.innerSize / 2,
        backgroundColor: COLORS.primaryLight,
        alignItems: 'center',
        justifyContent: 'center',
    },
    innerCircleChecked: {
        backgroundColor: COLORS.gray200,
    },
    faceIcon: {
        fontSize: CHECK_IN_BUTTON.iconSize,
    },
    checkIcon: {
        fontSize: CHECK_IN_BUTTON.iconSize,
        color: COLORS.success,
        fontWeight: 'bold',
    },
    statusText: {
        position: 'absolute',
        bottom: SPACING.xl,
        color: COLORS.textOnPrimary,
        fontSize: FONTS.size.lg,
        fontWeight: FONTS.semiBold as any,
    },
    statusTextChecked: {
        color: COLORS.textSecondary,
    },
});

export default CheckInButton;
