/**
 * ProfileScreen - 
 * 
 */
import React, { useState } from 'react';
import {
    View,
    Text,
    StyleSheet,
    ScrollView,
    SafeAreaView,
    TouchableOpacity,
    Image,
    Alert,
    TextInput,
    ActivityIndicator,
} from 'react-native';
import { GradientBackground } from '../../components';
import { COLORS, FONTS, SPACING, RADIUS, SHADOWS } from '../../theme';
import { APP_INFO } from '../../constants';
import { useAuth } from '../../contexts/AuthContext';
import { authService } from '../../services/api';

interface MenuItem {
    id: string;
    icon: string;
    title: string;
    subtitle?: string;
    onPress: () => void;
    showArrow?: boolean;
    danger?: boolean;
}

import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { RootStackParamList } from '../../types';

/**
 * 
 */
const ProfileScreen: React.FC = () => {
    const navigation = useNavigation<NativeStackNavigationProp<RootStackParamList>>();
    const { user, logout, refreshUser } = useAuth();

    // 
    const [isEditModalVisible, setIsEditModalVisible] = useState(false);
    const [editName, setEditName] = useState('');
    const [editPhone, setEditPhone] = useState('');
    const [editLineId, setEditLineId] = useState('');
    const [isUpdating, setIsUpdating] = useState(false);

    // 
    const openEditModal = () => {
        setEditName(user?.name || '');
        setEditPhone(user?.phoneNumber || '');
        setEditLineId(user?.lineId || '');
        setIsEditModalVisible(true);
    };

    /**
     * 
     */
    const handleSaveProfile = async () => {
        if (!editName) {
            Alert.alert('', '');
            return;
        }

        setIsUpdating(true);
        try {
            const response = await authService.updateProfile({
                name: editName,
                phoneNumber: editPhone,
                lineId: editLineId,
            });

            if (response.data) {
                //  Context
                await refreshUser();
                Alert.alert('', '');
                setIsEditModalVisible(false);
            } else {
                Alert.alert('', response.error?.message || '');
            }
        } catch (error: any) {
            Alert.alert('', error.message || '');
        } finally {
            setIsUpdating(false);
        }
    };

    /**
     * 
     */
    const handleLogout = () => {
        Alert.alert(
            '',
            '',
            [
                { text: '', style: 'cancel' },
                {
                    text: '',
                    style: 'destructive',
                    onPress: async () => {
                        await logout();
                        //  AuthContext 
                    },
                },
            ]
        );
    };

    /**
     * 
     */
    const menuItems: MenuItem[][] = [
        // 
        [
            {
                id: 'edit_profile',
                icon: '',
                title: '',
                onPress: openEditModal,
                showArrow: true,
            },
            {
                id: 'change_password',
                icon: '',
                title: '',
                onPress: () => Alert.alert('', ''),
                showArrow: true,
            },
        ],
        // ... () ...
        // 
        [
            {
                id: 'notification',
                icon: '',
                title: '',
                subtitle: '',
                onPress: () => Alert.alert('', ''),
                showArrow: true,
            },
            {
                id: 'message_templates',
                icon: '',
                title: '',
                subtitle: '',
                onPress: () => navigation.navigate('MessageTemplates'),
                showArrow: true,
            },
            {
                id: 'anomaly_rules',
                icon: '',
                title: '',
                subtitle: '',
                onPress: () => Alert.alert('', ''),
                showArrow: true,
            },
        ],
        // 
        [
            {
                id: 'privacy',
                icon: '',
                title: '',
                onPress: () => Alert.alert('', ''),
                showArrow: true,
            },
            {
                id: 'help',
                icon: '',
                title: '',
                onPress: () => Alert.alert('', ''),
                showArrow: true,
            },
            {
                id: 'about',
                icon: '',
                title: '',
                subtitle: ` ${APP_INFO.VERSION}`,
                onPress: () => Alert.alert('', ''),
                showArrow: true,
            },
        ],
        // 
        [
            {
                id: 'logout',
                icon: '',
                title: '',
                onPress: handleLogout,
                danger: true,
            },
        ],
    ];

    /**
     * 
     */
    const renderMenuItem = (item: MenuItem) => (
        <TouchableOpacity
            key={item.id}
            style={styles.menuItem}
            onPress={item.onPress}
            activeOpacity={0.7}
        >
            <Text style={styles.menuIcon}>{item.icon}</Text>
            <View style={styles.menuContent}>
                <Text style={[styles.menuTitle, item.danger && styles.menuTitleDanger]}>
                    {item.title}
                </Text>
                {item.subtitle && (
                    <Text style={styles.menuSubtitle}>{item.subtitle}</Text>
                )}
            </View>
            {item.showArrow && <Text style={styles.menuArrow}></Text>}
        </TouchableOpacity>
    );

    /**
     * 
     */
    const renderMenuGroup = (items: MenuItem[], index: number) => (
        <View key={index} style={styles.menuGroup}>
            {items.map((item, itemIndex) => (
                <React.Fragment key={item.id}>
                    {renderMenuItem(item)}
                    {itemIndex < items.length - 1 && <View style={styles.menuDivider} />}
                </React.Fragment>
            ))}
        </View>
    );

    return (
        <GradientBackground variant="light">
            <SafeAreaView style={styles.container}>
                <ScrollView
                    style={styles.scrollView}
                    contentContainerStyle={styles.scrollContent}
                    showsVerticalScrollIndicator={false}
                >
                    {/*  */}
                    <View style={styles.profileCard}>
                        {user ? (
                            <>
                                <View style={styles.avatarContainer}>
                                    <View style={styles.avatar}>
                                        <Text style={styles.avatarText}>
                                            {user.name?.charAt(0) || '?'}
                                        </Text>
                                    </View>
                                    <TouchableOpacity style={styles.editAvatarButton} onPress={openEditModal}>
                                        <Text style={styles.editAvatarIcon}></Text>
                                    </TouchableOpacity>
                                </View>
                                <Text style={styles.userName}>{user.name}</Text>
                                <Text style={styles.userEmail}>{user.email}</Text>
                                {user.phoneNumber && (
                                    <Text style={styles.userPhone}>{user.phoneNumber}</Text>
                                )}
                                {user.lineId && (
                                    <Text style={[styles.userPhone, { color: COLORS.success }]}>LINE: {user.lineId}</Text>
                                )}

                                {/*  */}
                                <View style={styles.statsContainer}>
                                    {/*  user  */}
                                    <View style={styles.statItem}>
                                        <Text style={styles.statValue}>-</Text>
                                        <Text style={styles.statLabel}></Text>
                                    </View>
                                    <View style={styles.statDivider} />
                                    <View style={styles.statItem}>
                                        <Text style={styles.statValue}>-</Text>
                                        <Text style={styles.statLabel}></Text>
                                    </View>
                                </View>
                            </>
                        ) : (
                            <View style={styles.notLoginContainer}>
                                <Text style={styles.notLoginText}></Text>
                                <TouchableOpacity
                                    style={styles.loginButton}
                                    onPress={() => {
                                        //  useNavigation
                                        const { useNavigation } = require('@react-navigation/native');
                                        const navigation = useNavigation();
                                        navigation.navigate('Auth');
                                    }}
                                >
                                    <Text style={styles.loginButtonText}></Text>
                                </TouchableOpacity>
                            </View>
                        )}
                    </View>

                    {/*  */}
                    {user && menuItems.map((group, index) => renderMenuGroup(group, index))}

                    {/*  */}
                    <View style={styles.footer}>
                        <Text style={styles.footerText}>
                            {APP_INFO.NAME} v{APP_INFO.VERSION}
                        </Text>
                        <Text style={styles.footerCopyright}>
                             2026 ALIVE. All rights reserved.
                        </Text>
                    </View>
                </ScrollView>

                {/*  Modal */}
                {isEditModalVisible && (
                    <View style={styles.modalOverlay}>
                        <View style={styles.modalContainer}>
                            <Text style={styles.modalTitle}></Text>

                            <Text style={styles.inputLabel}></Text>
                            <TextInput
                                style={styles.input}
                                value={editName}
                                onChangeText={setEditName}
                                placeholder=""
                            />

                            <TextInput
                                style={styles.input}
                                value={editPhone}
                                onChangeText={setEditPhone}
                                placeholder=""
                                keyboardType="phone-pad"
                            />

                            <Text style={styles.inputLabel}>LINE ID</Text>
                            <TextInput
                                style={styles.input}
                                value={editLineId}
                                onChangeText={setEditLineId}
                                placeholder=" LINE ID ()"
                                autoCapitalize="none"
                            />

                            <View style={styles.modalButtons}>
                                <TouchableOpacity
                                    style={[styles.modalButton, styles.cancelButton]}
                                    onPress={() => setIsEditModalVisible(false)}
                                >
                                    <Text style={styles.cancelButtonText}></Text>
                                </TouchableOpacity>
                                <TouchableOpacity
                                    style={[styles.modalButton, styles.saveButton]}
                                    onPress={handleSaveProfile}
                                    disabled={isUpdating}
                                >
                                    {isUpdating ? (
                                        <ActivityIndicator color={COLORS.white} />
                                    ) : (
                                        <Text style={styles.saveButtonText}></Text>
                                    )}
                                </TouchableOpacity>
                            </View>
                        </View>
                    </View>
                )}
            </SafeAreaView>
        </GradientBackground>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
    scrollView: {
        flex: 1,
    },
    scrollContent: {
        paddingHorizontal: SPACING.lg,
        paddingTop: SPACING.xl,
        paddingBottom: SPACING.xxxl,
    },
    profileCard: {
        backgroundColor: COLORS.cardBackground,
        borderRadius: RADIUS.xl,
        padding: SPACING.xl,
        alignItems: 'center',
        marginBottom: SPACING.xl,
        ...SHADOWS.md,
    },
    notLoginContainer: {
        alignItems: 'center',
        padding: SPACING.lg,
    },
    notLoginText: {
        fontSize: FONTS.size.lg,
        color: COLORS.textSecondary,
        marginBottom: SPACING.lg,
    },
    loginButton: {
        backgroundColor: COLORS.primary,
        paddingHorizontal: SPACING.xl,
        paddingVertical: SPACING.md,
        borderRadius: RADIUS.lg,
    },
    loginButtonText: {
        color: COLORS.white,
        fontSize: FONTS.size.md,
        fontWeight: FONTS.semiBold as any,
    },
    avatarContainer: {
        position: 'relative',
        marginBottom: SPACING.md,
    },
    avatar: {
        width: 80,
        height: 80,
        borderRadius: 40,
        backgroundColor: COLORS.primary,
        alignItems: 'center',
        justifyContent: 'center',
    },
    avatarText: {
        fontSize: 32,
        fontWeight: FONTS.bold as any,
        color: COLORS.white,
    },
    editAvatarButton: {
        position: 'absolute',
        bottom: 0,
        right: 0,
        width: 28,
        height: 28,
        borderRadius: 14,
        backgroundColor: COLORS.white,
        alignItems: 'center',
        justifyContent: 'center',
        ...SHADOWS.sm,
    },
    editAvatarIcon: {
        fontSize: 14,
    },
    userName: {
        fontSize: FONTS.size.xxl,
        fontWeight: FONTS.bold as any,
        color: COLORS.textPrimary,
        marginBottom: SPACING.xs,
    },
    userEmail: {
        fontSize: FONTS.size.md,
        color: COLORS.textSecondary,
        marginBottom: SPACING.xs,
    },
    userPhone: {
        fontSize: FONTS.size.md,
        color: COLORS.textSecondary,
        marginBottom: SPACING.lg,
    },
    statsContainer: {
        flexDirection: 'row',
        alignItems: 'center',
        paddingTop: SPACING.lg,
        borderTopWidth: 1,
        borderTopColor: COLORS.gray200,
    },
    statItem: {
        flex: 1,
        alignItems: 'center',
    },
    statValue: {
        fontSize: FONTS.size.title,
        fontWeight: FONTS.bold as any,
        color: COLORS.primary,
    },
    statLabel: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        marginTop: SPACING.xs,
    },
    statDivider: {
        width: 1,
        height: 40,
        backgroundColor: COLORS.gray200,
    },
    menuGroup: {
        backgroundColor: COLORS.cardBackground,
        borderRadius: RADIUS.lg,
        marginBottom: SPACING.md,
        overflow: 'hidden',
        ...SHADOWS.sm,
    },
    menuItem: {
        flexDirection: 'row',
        alignItems: 'center',
        padding: SPACING.lg,
    },
    menuIcon: {
        fontSize: 22,
        marginRight: SPACING.md,
        width: 28,
        textAlign: 'center',
    },
    menuContent: {
        flex: 1,
    },
    menuTitle: {
        fontSize: FONTS.size.md,
        fontWeight: FONTS.medium as any,
        color: COLORS.textPrimary,
    },
    menuTitleDanger: {
        color: COLORS.danger,
    },
    menuSubtitle: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        marginTop: 2,
    },
    menuArrow: {
        fontSize: FONTS.size.xxl,
        color: COLORS.textLight,
    },
    menuDivider: {
        height: 1,
        backgroundColor: COLORS.gray100,
        marginLeft: SPACING.lg + 28 + SPACING.md,
    },
    footer: {
        alignItems: 'center',
        paddingTop: SPACING.xl,
    },
    footerText: {
        fontSize: FONTS.size.sm,
        color: COLORS.textLight,
    },
    footerCopyright: {
        fontSize: FONTS.size.xs,
        color: COLORS.textLight,
        marginTop: SPACING.xs,
    },

    // Modal Styles
    modalOverlay: {
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0,0,0,0.5)',
        justifyContent: 'center',
        alignItems: 'center',
        padding: SPACING.lg,
        zIndex: 1000,
    },
    modalContainer: {
        backgroundColor: COLORS.white,
        borderRadius: RADIUS.lg,
        padding: SPACING.xl,
        width: '100%',
        maxWidth: 400,
        ...SHADOWS.lg,
    },
    modalTitle: {
        fontSize: FONTS.size.xl,
        fontWeight: FONTS.bold as any,
        color: COLORS.textPrimary,
        marginBottom: SPACING.lg,
        textAlign: 'center',
    },
    inputLabel: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        marginBottom: SPACING.xs,
        marginTop: SPACING.md,
    },
    input: {
        backgroundColor: COLORS.background,
        borderRadius: RADIUS.md,
        padding: SPACING.md,
        borderWidth: 1,
        borderColor: COLORS.gray200,
        fontSize: FONTS.size.md,
    },
    modalButtons: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        marginTop: SPACING.xl,
        gap: SPACING.md,
    },
    modalButton: {
        flex: 1,
        paddingVertical: SPACING.md,
        borderRadius: RADIUS.md,
        alignItems: 'center',
    },
    cancelButton: {
        backgroundColor: COLORS.gray100,
    },
    saveButton: {
        backgroundColor: COLORS.primary,
    },
    cancelButtonText: {
        color: COLORS.textPrimary,
        fontWeight: FONTS.medium as any,
    },
    saveButtonText: {
        color: COLORS.white,
        fontWeight: FONTS.bold as any,
    },
});

export default ProfileScreen;
