/**
 * StatusCard - 
 * 
 */
import React from 'react';
import {
    View,
    Text,
    StyleSheet,
    TouchableOpacity,
    ViewStyle,
} from 'react-native';
import { COLORS, FONTS, SPACING, RADIUS, SHADOWS } from '../../theme';

type CardVariant = 'success' | 'warning' | 'danger' | 'info' | 'default';

interface StatusCardProps {
    /**  */
    title: string;
    /**  */
    subtitle?: string;
    /**  */
    variant?: CardVariant;
    /**  */
    icon?: React.ReactNode;
    /**  */
    rightContent?: React.ReactNode;
    /**  */
    onPress?: () => void;
    /**  */
    style?: ViewStyle;
    /**  */
    children?: React.ReactNode;
}

/**
 * 
 */
const getVariantColors = (variant: CardVariant) => {
    switch (variant) {
        case 'success':
            return {
                background: '#E8F5E9',
                border: COLORS.success,
                text: COLORS.success,
            };
        case 'warning':
            return {
                background: '#FFF8E1',
                border: COLORS.warning,
                text: '#F57F17',
            };
        case 'danger':
            return {
                background: '#FFEBEE',
                border: COLORS.danger,
                text: COLORS.danger,
            };
        case 'info':
            return {
                background: '#E3F2FD',
                border: COLORS.info,
                text: '#1565C0',
            };
        default:
            return {
                background: COLORS.cardBackground,
                border: 'transparent',
                text: COLORS.textPrimary,
            };
    }
};

/**
 * 
 * @param title 
 * @param subtitle 
 * @param variant 
 * @param icon 
 * @param rightContent 
 * @param onPress 
 * @param style 
 */
const StatusCard: React.FC<StatusCardProps> = ({
    title,
    subtitle,
    variant = 'default',
    icon,
    rightContent,
    onPress,
    style,
    children,
}) => {
    const colors = getVariantColors(variant);

    const CardWrapper = onPress ? TouchableOpacity : View;
    const cardProps = onPress ? { onPress, activeOpacity: 0.7 } : {};

    return (
        <CardWrapper
            {...cardProps}
            style={[
                styles.container,
                {
                    backgroundColor: colors.background,
                    borderLeftColor: colors.border,
                    borderLeftWidth: variant !== 'default' ? 4 : 0,
                },
                style,
            ]}
        >
            <View style={styles.content}>
                {/*  */}
                {icon && (
                    <View style={styles.iconContainer}>
                        {typeof icon === 'string' ? <Text style={{ fontSize: 24 }}>{icon}</Text> : icon}
                    </View>
                )}

                {/*  */}
                <View style={styles.textContainer}>
                    <Text style={[styles.title, { color: colors.text }]}>{title}</Text>
                    {subtitle && <Text style={styles.subtitle}>{subtitle}</Text>}
                    {children}
                </View>

                {/*  */}
                {rightContent && <View style={styles.rightContainer}>{rightContent}</View>}
            </View>
        </CardWrapper>
    );
};

const styles = StyleSheet.create({
    container: {
        backgroundColor: COLORS.cardBackground,
        borderRadius: RADIUS.lg,
        padding: SPACING.lg,
        ...SHADOWS.sm,
    },
    content: {
        flexDirection: 'row',
        alignItems: 'center',
    },
    iconContainer: {
        marginRight: SPACING.md,
    },
    textContainer: {
        flex: 1,
    },
    title: {
        fontSize: FONTS.size.lg,
        fontWeight: FONTS.semiBold as any,
        color: COLORS.textPrimary,
    },
    subtitle: {
        fontSize: FONTS.size.md,
        color: COLORS.textSecondary,
        marginTop: SPACING.xs,
    },
    rightContainer: {
        marginLeft: SPACING.md,
    },
});

export default StatusCard;
