/**
 * HomeScreen - 
 * 
 */
import React, { useState, useCallback } from 'react';
import {
    View,
    Text,
    StyleSheet,
    ScrollView,
    SafeAreaView,
    TouchableOpacity,
    Alert,
    TextInput,
    Modal,
    KeyboardAvoidingView,
    Platform,
} from 'react-native';
import { CheckInButton, StatusCard, GradientBackground } from '../../components';
import { COLORS, FONTS, SPACING, RADIUS, SHADOWS } from '../../theme';
import { APP_INFO } from '../../constants';
import { checkinService } from '../../services/api';
import { useAuth } from '../../contexts/AuthContext';

import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { RootStackParamList } from '../../types';

/**
 * 
 * 
 */
const HomeScreen: React.FC = () => {
    const navigation = useNavigation<NativeStackNavigationProp<RootStackParamList>>();
    const { user, guestLogin } = useAuth();
    const [isCheckedIn, setIsCheckedIn] = useState(false);
    const [lastCheckInTime, setLastCheckInTime] = useState<Date | null>(null);
    const [isLoading, setIsLoading] = useState(false);
    const [guestPhone, setGuestPhone] = useState('');
    const [guestName, setGuestName] = useState('');
    const [debugToken, setDebugToken] = useState<string | null>(null);
    const [isGuestModalVisible, setIsGuestModalVisible] = useState(false);

    React.useEffect(() => {
        checkinService.getToken().then(t => setDebugToken(t));
    }, [user]);

    /**
     * 
     * 
     * return true = , false = 
     */
    const checkProfileCompletion = useCallback(() => {
        // 1.  ()
        // 1.  ()
        // 1.  () -  Modal
        if (!user) {
            setIsGuestModalVisible(true);
            return false;
        }

        // 2.  (  Email  LINE ID)
        // 
        const hasContactMethod = !!user.email || !!user.phoneNumber || !!user.lineId;

        // DEBUG: 
        console.log('Profile Check:', {
            email: !!user.email,
            phone: !!user.phoneNumber,
            line: !!user.lineId,
            result: hasContactMethod
        });

        if (!hasContactMethod) {
            Alert.alert(
                '',
                `\n1.  ${user.phoneNumber ? '()' : '()'}\n2. Email ${user.email ? '()' : '()'}\n3. LINE ID ${user.lineId ? '()' : '()'}`,
                [
                    { text: '', style: 'cancel' },
                    {
                        text: '',
                        onPress: () => navigation.navigate('Profile')
                    }
                ]
            );
            return false;
        }

        return true;
    }, [user, navigation]);

    /**
     * 
     *  API 
     */
    const handleCheckIn = useCallback(async () => {
        // 
        if (!checkProfileCompletion()) {
            return;
        }

        if (isCheckedIn) {
            Alert.alert('', '');
            return;
        }

        setIsLoading(true);

        try {
            //  API
            const response = await checkinService.createCheckIn();

            if (response.data) {
                setIsCheckedIn(true);
                setLastCheckInTime(new Date(response.data.checkIn.timestamp));

                Alert.alert(
                    '',
                    '',
                    [{ text: '', style: 'default' }]
                );
            } else {
                //  403 
                if (response.error?.code === 'PROFILE_INCOMPLETE') {
                    Alert.alert(
                        ' ()',
                        ' (Email  LINE)',
                        [
                            { text: '', style: 'cancel' },
                            {
                                text: '',
                                onPress: () => navigation.navigate('Profile') //  Profile  Modal
                            }
                        ]
                    );
                } else {
                    Alert.alert(
                        '',
                        `: ${response.error?.code}\n: ${response.error?.message}`,
                        [{ text: '', style: 'cancel' }]
                    );
                }
            }
        } catch (err: any) {
            Alert.alert('', err.message);
        } finally {
            setIsLoading(false);
        }
    }, [isCheckedIn, checkProfileCompletion]);

    /**
     * 
     */
    const formatTime = (date: Date | null): string => {
        if (!date) return '';
        return date.toLocaleTimeString('zh-TW', {
            hour: '2-digit',
            minute: '2-digit',
        });
    };

    /**
     * 
     */
    const getDateString = (): string => {
        const now = new Date();
        const options: Intl.DateTimeFormatOptions = {
            year: 'numeric',
            month: 'long',
            day: 'numeric',
            weekday: 'long',
        };
        return now.toLocaleDateString('zh-TW', options);
    };

    const handleGuestLogin = async () => {
        if (!guestPhone) {
            Alert.alert('', '');
            return;
        }
        if (guestPhone.length < 8) {
            Alert.alert('', '');
            return;
        }

        setIsLoading(true);
        try {
            const result = await guestLogin(guestPhone, guestName);
            if (result.success) {
                // Login success, close modal and trigger check-in automatically
                setIsGuestModalVisible(false);
                setGuestPhone('');
                setGuestName('');
                Alert.alert(
                    '',
                    '',
                    [{ text: '', onPress: () => handleCheckIn() }]
                );
            } else {
                Alert.alert('', result.error);
            }
        } catch (error) {
            Alert.alert('', '');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <GradientBackground variant="light">
            <SafeAreaView style={styles.container}>
                {/*  */}
                <View style={styles.header}>
                    <Text style={styles.headerTitle}></Text>
                    <TouchableOpacity
                        style={styles.settingsButton}
                        onPress={() => navigation.navigate('Profile')}
                    >
                        <Text style={styles.settingsIcon}></Text>
                    </TouchableOpacity>
                </View>

                <ScrollView
                    style={styles.scrollView}
                    contentContainerStyle={styles.scrollContent}
                    showsVerticalScrollIndicator={false}
                >
                    {/*  */}
                    <Text style={styles.dateText}>{getDateString()}</Text>

                    {/*  */}
                    <Text style={styles.statusHint}>
                        {isCheckedIn
                            ? ' '
                            : ''}
                    </Text>

                    {/*  */}
                    <View style={styles.checkInContainer}>
                        <CheckInButton
                            isCheckedIn={isCheckedIn}
                            onPress={handleCheckIn}
                            disabled={isLoading}
                        />
                        {lastCheckInTime && (
                            <Text style={styles.lastCheckInText}>
                                {formatTime(lastCheckInTime)}
                            </Text>
                        )}
                    </View>

                    {/*  */}
                    <View style={styles.sloganContainer}>
                        <Text style={styles.slogan}>{APP_INFO.SLOGAN}</Text>
                        <Text style={styles.sloganEn}>Daily check-in, all is well</Text>
                    </View>

                    {/*  */}
                    <View style={styles.cardsContainer}>
                        {/*  */}
                        <StatusCard
                            title=""
                            subtitle={isCheckedIn ? '' : ''}
                            variant={isCheckedIn ? 'success' : 'warning'}
                            rightContent={
                                <View style={[
                                    styles.statusBadge,
                                    isCheckedIn ? styles.statusBadgeSuccess : styles.statusBadgeWarning
                                ]}>
                                    <Text style={styles.statusBadgeText}>
                                        {isCheckedIn ? '' : '!'}
                                    </Text>
                                </View>
                            }
                            style={styles.card}
                        />

                        {/*    */}
                        {(!user || !user.id) ? (
                            <StatusCard
                                title=""
                                subtitle=" ()"
                                variant="warning"
                                onPress={() => setIsGuestModalVisible(true)}
                                rightContent={<Text style={styles.arrowIcon}></Text>}
                                style={styles.card}
                            />
                        ) : (
                            <StatusCard
                                title=""
                                subtitle=""
                                variant="danger"
                                onPress={() => navigation.navigate('Profile')}
                                rightContent={<Text style={styles.arrowIcon}></Text>}
                                style={styles.card}
                            />
                        )}

                        {/*  */}
                        <StatusCard
                            title=""
                            subtitle="  "
                            variant="info"
                            style={styles.card}
                        />
                    </View>
                </ScrollView>

                {/*  */}
                <View style={styles.footer}>
                    <Text style={styles.footerText}>
                        
                    </Text>
                    <View style={styles.footerLinks}>
                        <TouchableOpacity>
                            <Text style={styles.footerLink}></Text>
                        </TouchableOpacity>
                        <Text style={styles.footerDivider}></Text>
                        <TouchableOpacity>
                            <Text style={styles.footerLink}></Text>
                        </TouchableOpacity>
                    </View>
                    <Text style={styles.footerCopyright}>
                         2026 ALIVE. All rights reserved. (Build: 2026.01.23-LAZY-V1)
                    </Text>
                    {/* Debug Info Overlay */}
                    <View style={{ marginTop: 20, padding: 10, backgroundColor: '#eee', borderRadius: 8 }}>
                        <Text style={{ fontWeight: 'bold' }}>DEBUG INFO</Text>
                        <Text style={{ fontSize: 10, fontFamily: 'monospace' }}>
                            UID: {user?.id || 'null'} | Name: {user?.name || 'null'}
                        </Text>
                        <Text style={{ fontSize: 10, fontFamily: 'monospace' }}>
                            Email: {user?.email ? 'YES' : 'NO'} | Phone: {user?.phoneNumber ? 'YES' : 'NO'} | Line: {user?.lineId ? 'YES' : 'NO'}
                        </Text>
                        <Text style={{ fontSize: 10, fontFamily: 'monospace' }}>
                            CheckIn: {isCheckedIn ? 'TRUE' : 'FALSE'} | Token: {debugToken ? 'YES' : 'NO'}
                        </Text>
                    </View>
                </View>
                {/* Guest Login Modal */}
                <Modal
                    visible={isGuestModalVisible}
                    transparent={true}
                    animationType="slide"
                    onRequestClose={() => setIsGuestModalVisible(false)}
                >
                    <KeyboardAvoidingView
                        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
                        style={styles.modalOverlay}
                    >
                        <View style={styles.modalContent}>
                            <View style={styles.modalHeader}>
                                <Text style={styles.modalTitle}></Text>
                                <TouchableOpacity
                                    onPress={() => setIsGuestModalVisible(false)}
                                    style={styles.closeButton}
                                >
                                    <Text style={styles.closeButtonText}></Text>
                                </TouchableOpacity>
                            </View>

                            <Text style={styles.modalSubtitle}>
                                \n
                            </Text>

                            <Text style={styles.inputLabel}> ()</Text>
                            <TextInput
                                style={styles.guestInput}
                                placeholder="0912345678"
                                placeholderTextColor={COLORS.textLight}
                                value={guestPhone}
                                onChangeText={setGuestPhone}
                                keyboardType="phone-pad"
                            />

                            <Text style={styles.inputLabel}> ()</Text>
                            <TextInput
                                style={styles.guestInput}
                                placeholder="/"
                                placeholderTextColor={COLORS.textLight}
                                value={guestName}
                                onChangeText={setGuestName}
                            />

                            <TouchableOpacity
                                style={styles.guestButton}
                                onPress={handleGuestLogin}
                                disabled={isLoading}
                            >
                                {isLoading ? (
                                    <Text style={styles.guestButtonText}>...</Text>
                                ) : (
                                    <Text style={styles.guestButtonText}></Text>
                                )}
                            </TouchableOpacity>
                        </View>
                    </KeyboardAvoidingView>
                </Modal>
            </SafeAreaView>
        </GradientBackground >
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
    header: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingHorizontal: SPACING.lg,
        paddingTop: SPACING.lg,
        paddingBottom: SPACING.md,
    },
    headerTitle: {
        fontSize: FONTS.size.xxl,
        fontWeight: FONTS.bold as any,
        color: COLORS.textPrimary,
    },
    settingsButton: {
        padding: SPACING.sm,
    },
    settingsIcon: {
        fontSize: FONTS.size.xxl,
    },
    scrollView: {
        flex: 1,
    },
    scrollContent: {
        paddingHorizontal: SPACING.lg,
        paddingBottom: SPACING.xxxl,
    },
    dateText: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        textAlign: 'center',
        marginBottom: SPACING.sm,
    },
    statusHint: {
        fontSize: FONTS.size.md,
        color: COLORS.textSecondary,
        textAlign: 'center',
        marginBottom: SPACING.xxl,
    },
    checkInContainer: {
        alignItems: 'center',
        marginVertical: SPACING.xxl,
    },
    lastCheckInText: {
        marginTop: SPACING.lg,
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
    },
    sloganContainer: {
        alignItems: 'center',
        marginBottom: SPACING.xxl,
    },
    slogan: {
        fontSize: FONTS.size.lg,
        fontWeight: FONTS.medium as any,
        color: COLORS.primary,
        marginBottom: SPACING.xs,
    },
    sloganEn: {
        fontSize: FONTS.size.sm,
        color: COLORS.textLight,
    },
    cardsContainer: {
        gap: SPACING.md,
    },
    card: {
        marginBottom: SPACING.md,
    },
    statusBadge: {
        width: 28,
        height: 28,
        borderRadius: 14,
        alignItems: 'center',
        justifyContent: 'center',
    },
    statusBadgeSuccess: {
        backgroundColor: COLORS.success,
    },
    statusBadgeWarning: {
        backgroundColor: COLORS.warning,
    },
    statusBadgeText: {
        color: COLORS.white,
        fontSize: FONTS.size.md,
        fontWeight: FONTS.bold as any,
    },
    arrowIcon: {
        fontSize: FONTS.size.xxl,
        color: COLORS.textLight,
    },
    footer: {
        paddingHorizontal: SPACING.lg,
        paddingVertical: SPACING.lg,
        alignItems: 'center',
    },
    footerText: {
        fontSize: FONTS.size.xs,
        color: COLORS.textLight,
        textAlign: 'center',
        marginBottom: SPACING.sm,
    },
    footerLinks: {
        flexDirection: 'row',
        alignItems: 'center',
    },
    footerLink: {
        fontSize: FONTS.size.xs,
        color: COLORS.primary,
    },
    footerDivider: {
        fontSize: FONTS.size.xs,
        color: COLORS.textLight,
        marginHorizontal: SPACING.xs,
    },
    footerCopyright: {
        fontSize: FONTS.size.xs,
        color: COLORS.textLight,
        marginTop: SPACING.xs,
        fontWeight: 'bold',
    },
    guestFormCard: {
        backgroundColor: COLORS.white,
        borderRadius: RADIUS.lg,
        padding: SPACING.lg,
        marginBottom: SPACING.md,
        ...SHADOWS.md,
    },
    guestFormTitle: {
        fontSize: FONTS.size.lg,
        fontWeight: FONTS.bold as any,
        color: COLORS.textPrimary,
        marginBottom: SPACING.xs,
    },
    guestFormSubtitle: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        marginBottom: SPACING.md,
    },
    guestInput: {
        backgroundColor: COLORS.background,
        borderRadius: RADIUS.md,
        padding: SPACING.md,
        marginBottom: SPACING.sm,
        fontSize: FONTS.size.md,
        borderWidth: 1,
        borderColor: COLORS.gray300,
        color: COLORS.textPrimary,
    },
    guestButton: {
        backgroundColor: COLORS.primary,
        borderRadius: RADIUS.md,
        padding: SPACING.md,
        alignItems: 'center',
        marginTop: SPACING.sm,
    },
    guestButtonText: {
        color: COLORS.white,
        fontWeight: FONTS.bold as any,
        fontSize: FONTS.size.md,
    },
    modalOverlay: {
        flex: 1,
        backgroundColor: 'rgba(0,0,0,0.5)',
        justifyContent: 'center',
        padding: SPACING.lg,
    },
    modalContent: {
        backgroundColor: COLORS.white,
        borderRadius: RADIUS.lg,
        padding: SPACING.xl,
        ...SHADOWS.lg,
    },
    modalHeader: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginBottom: SPACING.md,
    },
    modalTitle: {
        fontSize: FONTS.size.xl,
        fontWeight: FONTS.bold as any,
        color: COLORS.textPrimary,
    },
    modalSubtitle: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        marginBottom: SPACING.lg,
        lineHeight: 20,
    },
    closeButton: {
        padding: SPACING.xs,
    },
    closeButtonText: {
        fontSize: FONTS.size.lg,
        color: COLORS.textLight,
    },
    inputLabel: {
        fontSize: FONTS.size.sm,
        color: COLORS.textSecondary,
        marginBottom: SPACING.xs,
        marginTop: SPACING.sm,
    },
});

export default HomeScreen;
