/**
 * 
 * 
 */
import AsyncStorage from '@react-native-async-storage/async-storage';
import { apiRequest, authenticatedRequest, AUTH_TOKEN_KEY, ApiResponse } from './config';

/**
 * 
 */
export interface User {
    id: number;
    email: string;
    name: string;
    phoneNumber?: string;
    lineId?: string; // Added lineId
    avatarUrl?: string;
}

/**
 * 
 */
export interface RegisterParams {
    email: string;
    password: string;
    name: string;
    phoneNumber?: string;
}

/**
 * 
 */
export interface UpdateProfileParams {
    name?: string;
    phoneNumber?: string;
    lineId?: string; // Added lineId
}

/**
 * 
 */
export interface LoginParams {
    email: string;
    password: string;
}

/**
 * 
 */
export interface AuthResponse {
    token: string;
    user: User;
}

class AuthService {
    /**
     * 
     */
    async register(params: RegisterParams): Promise<ApiResponse<AuthResponse>> {
        const response = await apiRequest<AuthResponse>('/auth/register', {
            method: 'POST',
            body: JSON.stringify(params),
        });

        //  token
        if (response.data) {
            await this.saveToken(response.data.token);
        }

        return response;
    }

    /**
     * 
     */
    async login(params: LoginParams): Promise<ApiResponse<AuthResponse>> {
        const response = await apiRequest<AuthResponse>('/auth/login', {
            method: 'POST',
            body: JSON.stringify(params),
        });

        //  token
        if (response.data) {
            await this.saveToken(response.data.token);
        }

        return response;
    }

    /**
     *  ()
     */
    async guestLogin(params: { phoneNumber: string; name?: string }): Promise<ApiResponse<AuthResponse>> {
        const response = await apiRequest<AuthResponse>('/auth/guest-login', {
            method: 'POST',
            body: JSON.stringify(params),
        });

        //  token
        if (response.data) {
            await this.saveToken(response.data.token);
        }

        return response;
    }

    /**
     * 
     */
    async getMe(): Promise<ApiResponse<User>> {
        const token = await this.getToken();
        if (!token) {
            return {
                error: {
                    code: 'NO_TOKEN',
                    message: '',
                },
            };
        }

        return authenticatedRequest<User>('/auth/me', token, {
            method: 'GET',
        });
    }

    /**
     * 
     */
    async updateProfile(params: UpdateProfileParams): Promise<ApiResponse<{ user: User }>> {
        const token = await this.getToken();
        if (!token) {
            return {
                error: {
                    code: 'NO_TOKEN',
                    message: '',
                },
            };
        }

        return authenticatedRequest<{ user: User }>('/user/profile', token, {
            method: 'PUT',
            body: JSON.stringify(params),
        });
    }

    /**
     * 
     */
    async logout(): Promise<void> {
        await AsyncStorage.removeItem(AUTH_TOKEN_KEY);
    }

    /**
     *  Token
     */
    async saveToken(token: string): Promise<void> {
        await AsyncStorage.setItem(AUTH_TOKEN_KEY, token);
    }

    /**
     *  Token
     */
    async getToken(): Promise<string | null> {
        return await AsyncStorage.getItem(AUTH_TOKEN_KEY);
    }

    /**
     * 
     */
    async isAuthenticated(): Promise<boolean> {
        const token = await this.getToken();
        return !!token;
    }
}

export default new AuthService();
